.scheduleTale{
    background-color: white;
    border-radius: 14px 14px 0 0;
    height: calc(100vh - 62px - 43px - 48px);
    z-index: 999;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.scheduleSliderContainer{
    height: 100%;
    /*border-radius: 14px 14px 0 0;*/
    margin-top: 62px;
}

.scheduleSliderContainer > div > div > .Gallery__slide{
    margin-right: 10px;
}

.scheduleSliderContainer > div > .Gallery__layer{
    -webkit-transition: rotate 0.24s, -webkit-transform  0.24s cubic-bezier(0.1, 0, 0.25, 1) 0s !important;
    transition: rotate 0.24s, -webkit-transform  0.24s cubic-bezier(0.1, 0, 0.25, 1) 0s !important;
    transition: rotate 0.24s, transform  0.24s cubic-bezier(0.1, 0, 0.25, 1) 0s !important;
    transition: rotate 0.24s, transform  0.24s cubic-bezier(0.1, 0, 0.25, 1) 0s, -webkit-transform  0.24s cubic-bezier(0.1, 0, 0.25, 1) 0s !important;
    /*transform: rotate(0deg) !important;*/
    /*transform: rotate(45deg) !important;*/
}

.scheduleSliderContainer > div > .Gallery__layer:active{
    /*transition: rotate 0.24s, transform  0.24s cubic-bezier(0.1, 0, 0.25, 1) 0s !important;*/
    /*transform: rotate(60deg) !important;*/
    /*transform: translateX(-330px);*/
}

.scheduleSliderContainer > div:last-child{
    -webkit-transition: -webkit-transform 0.24s cubic-bezier(.77,0,.18,1) 0s !important;
    transition: -webkit-transform 0.24s cubic-bezier(.77,0,.18,1) 0s !important;
    transition: transform 0.24s cubic-bezier(.77,0,.18,1) 0s !important;
    transition: transform 0.24s cubic-bezier(.77,0,.18,1) 0s, -webkit-transform 0.24s cubic-bezier(.77,0,.18,1) 0s !important;
}
